/*Contains layout for entire website outside of App Container*/
*{
  box-sizing:border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#333333;
  color:#cccccc;
}

footer{
  color:white;
  text-align:center;
  font-weight:bold;
  padding-top:20px;
  height:60px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a{
  text-decoration: none;
  color:dodgerblue;
}

a:hover{
  text-decoration:underline;
}

a:active{
  color:orange;
}

/*=======================================================
//  Font Awesome Styles
//=======================================================*/

.fa-exclamation-triangle
{
  color: goldenrod;
}

.fa-windows
{
  color: #0078d7;
}

.fa-android
{
  color: #a3c439;
}
/*CSS file that controls layout and defaults of NiceRa.in App*/
main{
    padding-top:60px; /*size of nav bar*/
}

/*CSS styling for header*/

header{
    background-color:black;
    color:white;
    position:fixed;
    width:100%;
    height:60px;
    font-family: 'Nunito', sans-serif;
    z-index:10;
}

nav{
    /* height:60px; */
    vertical-align:top;
    height:100%;
    display:inline-block;
}

header img {
    width:50px;
    height:50px;
}

header ul{
    list-style:none;
    margin:0;
    height:inherit;
    padding-left:10px;
}

header li{
    display:inline;
    vertical-align: middle;
}

.header-logo
{
    height:50px;
    width:50px;
    margin-top:5px;
    margin-left:5px;
    padding:none;
    border:none;
    background:url('/images/drop50.png');
}

.header-logo:hover, .header-logo:focus{
    border:none;
}

header button{
    height:100%;
    width:auto;
    background-color:black;
    color:white;
    border:none;
    border-color:#0099ff;
    font-size:1.2em;
    cursor:pointer;
}

header button:hover, button:focus{
    color:#0099ff;
    -webkit-transition: color 0.5s, border 0.4s;
    transition: color 0.5s, border 0.4s;
    border-top:4px solid;
    border-bottom: 4px solid;
}
.image-hero
{
    height:100vh;
    background-image:url("/images/hero.jpg");
    background-size:cover;
    background-repeat:no-repeat;
}

.home-page h2{
    text-align:center;
    font-family: 'Abel', sans-serif;
    text-decoration:underline;
    position:relative;
    font-weight:500;
    font-size:2.2em;
}

.home-page section{
    padding-bottom:20px;
    min-height:250px;
}

/* .home-page section:nth-child(odd){
    padding-top:15px;
    background-color:#2C2C2C;
} */

.middle-content
{
    background-color:#2C2C2C;
    padding-top:20px;
}

.home-container .signature
{
    padding-left:4em;
    padding-top:20px;
}

.project-container
{
    display:flex;
    flex-direction:row;
    flex-wrap:wrap;
    justify-content:center;
}

.home-container
{
    max-width:1200px;
    margin:0 auto;
}

.home-container p
{
    padding-left:10px;
    padding-right:10px;
}

footer{
    border-top:1px solid #bbb;
    color:#bbb;
}

.project-cell
{
    width:500px;
    height:300px;
    background-color:#222;
    margin:5px;
    overflow:hidden;
    position:relative;
    border:1px solid black;
    cursor:pointer;

    /* No Selection */
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
     user-select: none;
}

.project-cell-logo
{
    position:absolute;
    left:0;
    top:0;
    -webkit-transition:0.75s;
    transition:0.75s;
    height:100%;
    width:100%;
    z-index:1;
    box-shadow:0px 2px 3px #111;
}

.project-cell-description
{
    position:absolute;
    bottom:0;
    left:0;
    padding:10px;
    height:175px;
}
/* ------------------------------------------------ */
/* Contact Form Section */
.contact-form
{
    max-width:600px;
    margin-top:20px;
    margin-left:auto;
    margin-right:auto;
}

.contact-form input, .contact-form textarea {
    padding: 5px;
    display:flex;
    min-width:0px;
	width:100%;
	font-family:Verdana, Helvetica, sans-serif;
	font-size: 1em;
	margin: 3px 0px 10px 0px;
	border: 2px solid #ccc;
}

.contact-form textarea {
	height: 90px;
}

.contact-form textarea:focus, .contact-form input:focus {
	border: 2px solid #78b2cb;
}

.contact-form fieldset{
    border:none;
}

.contact-form button {
	max-width: 100px;
    background-color: white;
    box-shadow: none;
    border: 2px solid #ccc;
    padding: 8px;
    margin-top: 10px;
    margin-bottom:10px;
    margin-right:12px;
    float:right;
}

.contact-form button:hover{
    background-color:#eee;
}

.clear
{
    clear:both;
}
.project-page
{
    background-size:cover;
    background-repeat:no-repeat;
    padding:0;
    background-color:#eee;
}

.project-page-content main{
    padding-top:0px;
}

.project-page h2{
    margin-top:0px;
    padding-top:20px;
    font-family: 'Abel', sans-serif;
    text-decoration:underline;
    position:relative;
    font-weight:500;
    font-size:2.2em;
    margin-bottom:10px;
}

.project-page h3{
    margin-top:0px;
    padding-top:20px;
    font-family: 'Abel', sans-serif;
    position:relative;
    font-weight:500;
    font-size:1.8em;
}

.project-page h4{
    font-size: 1.3em;
}

.project-page footer{
    border-top:1px solid #eee;
    padding-top:20px;
}

/*=============================================
// Header Classes
//===========================================*/

/*Overall Header Container*/
.project-header
{
    width:100%;
    background-color:#000;
    font-size:0; /*removes our gap*/
    height:70vh;
    position: relative;
}

/*Button to download, read more, etc.*/
.project-header button{
    border: 1px solid white;
    padding: 12px;
    background-color:dodgerblue;
    color:white;
    font-size:0.5em;
    font-weight: bold;
    display:block;
    margin: 0 auto;

    -webkit-transition: all 50ms ease;
    transition: all 50ms ease;
}

.project-header button:hover{
    background-color:rgb(86, 166, 247);
}

.project-header a:hover{
    text-decoration:none;
}

.project-header a:active{
    color:inherit;
}

/*Contains button and logo*/
.project-header-wrapper
{
    width:100%;
    height:100%;
    display:flex;
    flex-direction: column;
    top:0;
    left:0;
    justify-content:center;
    z-index:5;
    position: absolute;
    font-size:2rem;
}

/*Logo div within our content box*/
.project-header-content
{
    margin:0 auto;
    text-align:center;
}

/*Styling for our logo*/
.project-header-logo-wrapper{
    width: 100%;
    min-width: 10rem;
    max-width: 27rem;
    margin: 0 auto;
}

/*Picture background within header*/
.project-header picture{
    display:flex;
    height:inherit;
}


/*Default fallback img located in picture tag*/
.project-header img{
    object-fit: cover;
    height: auto;
    width: 100%;
}

/*=============================================
// Page Content Classes
//===========================================*/

/*Wraps all project content to 1200px*/
.project-page-content
{
    max-width:1200px;
    margin:0 auto;
    background-color:rgba(0, 0, 0, 0.4);
}

.project-pad{
    padding-left:20px;
    padding-right:20px;
}

.project-downloads ul, .project-links ul
{
    padding-left:20px;
}

.project-downloads li, .project-links li{
    list-style: none;
}

.project-page-content i{
    margin-right: 0.5em;
}

/*Content overlay that darkens the background image*/
.project-page-wrapper
{
    background-color:rgba(0, 0, 0, 0.6);
}

.trailer-container {
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
}
.trailer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.media-container
{
    display: block;
    min-height: 1px;
    width: 100%;
    /* border: 1px solid rgb(221, 221, 221); */
    overflow: auto;
}

.media-container img {
    filter: brightness(100%);
    -webkit-filter: brightness(100%);
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
}

.media-container img:hover {
    filter: brightness(70%);
    -webkit-filter: brightness(70%);
    
}

.project-media
{
    margin-top:20px;
    border-top:1px solid black;
}
